/* Variable/Settings */
.switch {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 60px;
  z-index: 3;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch .slider-switch {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.switch .slider-switch:before {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  background-color: #ffde00;
  transition: 0.4s;
}
.switch .round:before {
  border-radius: 25px;
  box-shadow: 0px 2px 3px rgba(0,0,0,0.15);
}
.switch .switch-text-box {
  pointer-events: none;
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-top: 15px;
  font-weight: 700;
}
.switch .switch-text-box label {
  width: 50%;
  text-align: center;
}
.switch .switch-text-box .on {
  position: relative;
  justify-content: center;
  color: #000;
  transition: 0.4s;
}
.switch .switch-text-box .off {
  position: relative;
  color: #eecf00;
  transition: 0.4s;
}
.switch input:checked + .slider-switch:before {
  transform: translateX(100%);
}
.switch input:checked + .slider-switch .on {
  color: #eecf00;
  transition: 0.4s;
}
.switch input:checked + .slider-switch .off {
  color: #000;
  transition: 0.4s;
}
.slick-slider .slick-slide {
