  padding: 50px 0;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column:last-child {
    padding: 160px 0;
    margin-bottom: 35px;
  }
}
section.question-container .column:last-child:after {
  background: transparent url("../images/promotion/left-arrow.png") no-repeat;
  left: -15px;
  right: auto;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column:last-child:after {
    left: -30px;
  }
}
section.question-container .column:last-child:before {
  content: "";
  display: block;
  width: 200px;
  height: 160px;
  background: transparent url("../images/happy-emoji.png") no-repeat;
  position: absolute;
  right: 0px;
  bottom: -10px;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column:last-child:before {
    right: -75px;
  }
}
@media only screen and (max-width: 1100px) {
  section.question-container .column:last-child:before {
    transform: scale(0.5) rotate(-40deg);
    left: -80px;
    top: 0;
  }
}
section.question-container .column .headline {
  color: #fff;
  max-width: 500px;
  text-align: center;
  position: relative;
  padding: 0 35px;
  margin: 0 auto;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column .headline {
    padding-left: calc(50% - 30px);
    padding-right: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    text-align: left;
    margin: 0;
  }
}
section.question-container .column .headline p {
  margin: 15px 0 30px;
}
section.question-container .column .headline a {
  display: none;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column .headline a {
    display: block;
  }
}
section.question-container .column .visual-phone {
  max-width: 510px;
  margin: 0 auto;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column .visual-phone {
    position: absolute;
    top: -95%;
    left: -30px;
    margin: 0;
  }
}
section.question-container .column .visual-phone a {
  display: block;
  margin: 30px auto 0;
}
@media only screen and (min-width: 1100px) {
  section.question-container .column .visual-phone a {
    display: none;
  }
}
section.question-container .notice {
  margin: 0;
}
@media only screen and (min-width: 1100px) {
  section.question-container .notice {
    font-size: 18px;
    line-height: 28px;
  }
}
section.question-container .notice a {
  text-decoration: underline;
}
section.app-wrapper {
  background: #ffde00;
  border-radius: 35px 35px 0 0;
  padding: 60px 0 50px;
}
@media only screen and (min-width: 769px) {
  section.app-wrapper {
    border-radius: 92px 92px 0 0;
    padding: 100px 0;
  }
}
section.app-wrapper ul {
  list-style-type: none;
  padding: 20px 0 0;
  margin: 0;
  justify-content: center;
  flex-direction: row;
}
section.app-wrapper ul li {
  margin: 0 5px;
}
@media only screen and (max-width: 769px) {
  section.app-wrapper ul li {
    flex: 0 1 49%;
    margin: 0;
  }
}
@media only screen and (min-width: 1100px) {
  section.app-wrapper ul li .visual {
    max-width: 200px;
  }
}
body {
  font-family: 'Lato';
  font-weight: 400;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
  color: #000;
  box-sizing: border-box;
  overflow-x: hidden;
}
main.ssl-customer-landing-wrapper {
  background-color: #fffceb;
}
main.ssl-customer-landing-wrapper img {
  max-width: 100%;
  display: block;
}
main.ssl-customer-landing-wrapper a {
  text-decoration: none;
  color: #000;
}
main.ssl-customer-landing-wrapper a.btn--primary,
main.ssl-customer-landing-wrapper a.btn--secondary {
  background: #ffde00;
  color: #000;
  font-size: 16px;
  line-height: 22px;
  transition: 0.3s ease-in-out;
  font-weight: 700;
  max-width: 160px;
  padding: 12px 0;
  border-radius: 50px;
  display: block;
  text-align: center;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper a.btn--primary,
  main.ssl-customer-landing-wrapper a.btn--secondary {
    max-width: 220px;
    padding: 15px 0;
    font-size: 18px;
  }
}
main.ssl-customer-landing-wrapper a.btn--primary:hover,
main.ssl-customer-landing-wrapper a.btn--secondary:hover {
  background-color: #fff;
  color: #000;
}
main.ssl-customer-landing-wrapper a.btn--secondary {
  background: #fff;
}
main.ssl-customer-landing-wrapper a.link {
  text-decoration: underline;
  color: #000;
}
main.ssl-customer-landing-wrapper .mob-hide {
  display: none;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper .mob-hide {
    display: block;
  }
}
main.ssl-customer-landing-wrapper .mob-show {
  display: block;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper .mob-show {
    display: none;
  }
}
main.ssl-customer-landing-wrapper .content-wrapper {
  width: auto;
  max-width: 1060px;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
}
main.ssl-customer-landing-wrapper .content-wrapper:after {
  content: "";
  display: block;
  clear: both;
}
main.ssl-customer-landing-wrapper h2 {
  font-weight: 900;
  font-size: 24px;
  line-height: 30px;
  margin: 0;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper h2 {
    font-size: 36px;
    line-height: 44px;
  }
}
main.ssl-customer-landing-wrapper h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  margin: 0;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper h3 {
    font-size: 26px;
    line-height: 28px;
  }
}
main.ssl-customer-landing-wrapper p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
@media only screen and (min-width: 1100px) {
  main.ssl-customer-landing-wrapper p {
    font-size: 20px;
    line-height: 28px;
  }
}
main.ssl-customer-landing-wrapper p strong {
  font-weight: 700;
}
main.ssl-customer-landing-wrapper .text-center {
  text-align: center;
}
main.ssl-customer-landing-wrapper .text-left {
  text-align: left;
}
